.radiooption {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: white;
  border: 1px solid #ddd;
  cursor: pointer;
}

.radiooption:first-of-type {
  border-top: 1px solid #ddd;
}

.radiooption input[type="radio"] {
  display: none;
}

.radiooption span {
  font-size: 14px;
  color: #333;
}

.radiooption input[type="radio"]:checked + span::before {
  background-color: #007bff; /* Customize color */
  border-color: #007bff;
}

.radiooption span::before {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 10px;
  border: 2px solid #ccc;
  border-radius: 50%;
  background-color: #f1f1f1;
}

.radiooption input[type="radio"]:checked + span::before {
  background-color: black;
  border: 2px solid black;
}

.radiooptions {
  display: inline-flex;
  gap: 10px; 
}

.buttons {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttons:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttons:active {
  background-color: #222;
  transform: scale(1);
}
