}
.pad {
  padding-right: 30px;
}
.header {
  background-color: rgb(250, 246, 246);
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding: 10px 0px; */
  font-family: "Trebuchet MS", sans-serif;
  background-color: yellow;
}

.header .logo {
  font-size: 24px;
  font-weight: bold;
