.unbanline {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ccc; /* Optional: For visual separation */
}

.lineContent {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.button {
  margin-bottom: 10px;
  padding: 10px;
  background-color: #263146;
  color: white;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  width: 250px;
  align-self: center;}

.search {
margin-top:10px;
}

.button:hover {
  background-color: #1b2331;
}
.label{
	padding-bottom:10px;
}
.unbanlist{
	max-height:80%;
	overflow-y:scroll;
	font-size:20px;
	border:1px solid black;
  margin-top:2%;
  font-size:25px;
}	
