/* Header Styling */
.header {
  background-image: url("Pictures/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: yellow; /* Consistent background color */
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Trebuchet MS", sans-serif;
  padding: 0 20px;
  box-sizing: border-box;
}

.header .logo {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  padding-left: 30px;
}

.logocontainer {
  display: flex;
  align-items: center;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  margin-left: 10px;
}

/* Navigation Styling */
.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
  font-size: 16px;
}

.nav ul li a:hover {
  color: #263146; /* Hover effect on links */
}

/* Button Styling */
.button, .topbutton {
  padding: 10px;
  background-color: #263146;
  color: white;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  width: 250px;
  text-align: center;
}

.button:hover, .topbutton:hover {
  background-color: #1b2331;
}

.button:active, .topbutton:active {
  background-color: #222;
}

/* Main Content */
.main {
  padding: 100px 20px 20px; /* Adjust for header and footer */
  text-align: center;
}

.buttoncontainer {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 60px;
}

/* Title Styling */
.title {
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 30px;
}

/* Footer Styling */
.footer {
  background-image: url("Pictures/footer.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 20px;
  margin-top: 20px;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
  font-size: 14px;
}

.footerlinks a:hover {
  color: #263146;
}

/* Link Styling */
.Link {
  text-decoration: none;
  color: black;
}
.buttons {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}
