/* Padding Helper */
.pad {
  padding-right: 30px;
}

/* Header */
.header {
  background-image: url("Pictures/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: yellow;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Trebuchet MS", sans-serif;
}

.header .logo {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  padding-left: 30px;
}

/* Table */
.table {
  width: 65%; 
  margin: 0 auto;
  border-collapse: collapse;
}

.td {
  padding: 20px; 
  vertical-align: middle; 
  text-align: center;
}

/* Navigation */
.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
}

/* Form Styles */
.form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.label {
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 5px;
  color: black; /* Ensures label color is consistent */
}

input[type="text"],
input[type="email"],
textarea,
input[type="file"],
.button {
  margin-top: 5px;
  padding: 8px;
  font-size: 16px;
}

/* Button Styling */
.button {
  margin-top: 25px;
  padding: 10px;
  background-color: #263146;
  color: white;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  width: 250px;
  align-self: center;
}

.button:hover {
  background-color: #1b2331;
}

/* Footer */
.footer {
  background-image: url(Pictures/footer.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 20px;
  margin-top: 20px;
}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

/* Spacing for Content */
.spacetop {
  margin-top: 130px;
}

/* Table Layout for Images & Text */
.tableText {
  width: 50%;
  position: relative;
  top: 80px; 
  vertical-align: top;
}

.tableImage {
  width: 100%;
  max-width: 400px;
  height: auto; 
}

.normaltext {
  color: black;
  text-align: center;
}

/* Form Container */
.formcontainer {
  flex: 1;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: -190px;
}

/* Input Styling */
input[type="text"],
input[type="password"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 15px;
  font-size: 1em;
}

/* Button Styling for Actions */
.cancel-btn,
.create-btn {
  flex: 1;
  padding: 10px;
  font-size: 1em;
  border-radius: 5px;
  cursor: pointer;
}

.buttons {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.cancel-btn {
  background-color: #fff;
  border: 1px solid #000;
  color: #000;
}

/* Account Addition Section */
.addaccountcontainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #f9f9f9;
  gap: 20px; 
}

.accountinfo {
  display: flex;
  align-items: center;
  gap: 15px;
}

.text h2 {
  font-size: 18px;
  font-weight: 600;
}

.text p {
  font-size: 14px;
  color: #777;
}

/* Gender Selection */
.genderselection {
  font-family: Arial, sans-serif;
  max-width: 200px;
}

.radiooption {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: white;
  border: 1px solid #ddd;
  cursor: pointer;
}

.radiooption:first-of-type {
  border-top: 1px solid #ddd;
}

.radiooption input[type="radio"] {
  display: none;
}

.radiooption span {
  font-size: 14px;
  color: #333;
}

.radiooption input[type="radio"]:checked + span::before {
  background-color: #007bff; /* Customize color */
  border-color: #007bff;
}

.radiooption span::before {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 10px;
  border: 2px solid #ccc;
  border-radius: 50%;
  background-color: #f1f1f1;
}

.radiooption input[type="radio"]:checked + span::before {
  background-color: black;
  border: 2px solid black;
}

.radiooptions {
  display: inline-flex;
  gap: 10px; 
}

/* Logo Container */
.logocontainer {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  margin-left: 10px;
}

/* Link Styling */
.Link {
  text-decoration: none;
  color: black;
}

/* Enhanced Button Styling */
.buttonse {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttonse:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttonse:active {
  background-color: #222;
  transform: scale(1);
}

/* Responsive Design for Small Devices */
@media (max-width: 1024px) {
  .header {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .nav ul {
    flex-direction: column;
    gap: 10px;
  }

  .formcontainer {
    margin-top: 0;
  }

  .table {
    width: 90%;
  }
}

@media (max-width: 768px) {
  .formcontainer {
    padding: 20px;
  }

  .buttonse {
    width: 100%;
  }
}
