/* Padding Helper */
.pad {
  padding-right: 30px;
}

/* Header */
.header {
  background-image: url("Pictures/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: yellow; /* overriding the background-color from header's second definition */
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Trebuchet MS", sans-serif;
}

.header .logo {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
}

/* Navigation */
.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
}

/* Ban Section */
.ban {
  height: 367px;
  width: 70%;
  margin-left: 15%;
  margin-right: 15%;
  background-color: #ffffff;
}

.ban h1 {
  text-align: center;
  margin-top: 140px;
}

.ban form {
  text-align: center;
}

.inputweb {
  width: 90%;
  height: 50px;
  text-align: center;
}

/* Button Styling */
.button {
  margin-top: 25px;
  padding: 10px;
  background-color: #263146;
  color: white;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  width: 250px;
  align-self: center;
}

.button:hover {
  background-color: #1b2331;
}

.label {
  padding-bottom: 10px;
}

/* Footer */
.footer {
  background-image: url(Pictures/footer.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 0px;
  margin-top: 100px;
  position: absolute;
  bottom: 0px;
  width: 100%;
}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

/* Logo Section */
.logocontainer {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  margin-left: 10px;
}

/* Link Styling */
.Link {
  text-decoration: none;
  color: black;
}

/* Buttons */
.buttons {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttons:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttons:active {
  background-color: #222;
  transform: scale(1);
}

/* Responsive Design for Small Devices */
@media (max-width: 1024px) {
  .header {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .nav ul {
    flex-direction: column;
    gap: 10px;
  }

  .ban {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
  }

  .button {
    width: 100%;
    padding: 15px;
  }
}

@media (max-width: 768px) {
  .ban {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
  }

  .button {
    width: 100%;
    padding: 15px;
  }
}

@media (max-width: 480px) {
  .footer {
    padding: 15px 10px;
  }

  .button {
    width: 100%;
    padding: 20px;
  }
}
