.body {
  display: flex;
  justify-content: center;
  align-items: center;
height:643px;
}
/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Trebuchet MS", sans-serif;
  background-color: #f9f9f9;
}

/* Header */
.header {
  background-image: url("Pictures/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: black;
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: yellow;
  z-index: 1000;
}

.header .logo {
  font-size: 24px;
  font-weight: bold;
  padding-left: 30px;
}

.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
  padding-right: 30px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
}

.nav ul li a:hover {
  text-decoration: underline;
}

/* Login Section */
.loginleft {
  flex: 1;
  padding: 40px;
  text-align: center;
  margin-top: -20px;
}

.loginleft h1 {
  font-size: 2em;
  font-weight: bold;
}

.loginleft p {
  margin-top: 10px;
  font-size: 1em;
  color: #666;
}

/* Container */
.container {
  display: flex;
  width: 100%;
  max-width: 100vh;
  min-height: 200px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  align-items: center;
  margin-top: 200px;
}

/* Right Side Login Form */
.loginright {
  flex: 1;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 100px;
}

.form label {
  display: block;
  margin: 15px 0 5px;
  font-weight: bold;
}

.accounttype {
  display: flex;
  gap: 10px;
}

.accountbtn {
  padding: 10px 20px;
  margin: 5px;
  border: 1px solid #ccc;
  background-color: #f5f5f5;
  color: black;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.accountbtn:hover,
.accountbtn:focus {
  background-color: #ddd;
  color: black;
}

.accountbtn.selected {
  background-color: #007bff;
  color: white;
  border-color: #0056b3;
}

.accountbtn.selected:hover {
  background-color: #0056b3;
}

/* Input Fields */
.input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 15px;
  font-size: 1em;
}

/* Buttons */
.buttons {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.cancelbtn,
.loginbtn {
  margin-top: 10px;
  padding: 10px;
  background-color: #263146;
  color: white;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  width: 190px;
  align-self: center;
}

.cancelbtn {
  background-color: #000;
  border: 1px solid #000;
}

.cancelbtn:hover,
.loginbtn:hover {
  background-color: #1b2331;
}

.loginbtn {
  background-color: #000;
}

.footer {
  background-image: url("Pictures/footer.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 20px;
  margin-top: 207px;
}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

.logocontainer {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  margin-left: 10px;
}

/* Link Styling */
.Link {
  text-decoration: none;
  color: black;
}

/* Button Animations */
.buttonsa {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttonsa:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttonsa:active {
  background-color: #222;
  transform: scale(1);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .header {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .nav ul {
    flex-direction: column;
    gap: 10px;
  }

  .loginleft,
  .loginright {
    padding: 20px;
  }

  .container {
    margin-top: 20px;
  }
}

@media (max-width: 768px) {
  .header {
    font-size: 18px;
  }

  .loginleft h1 {
    font-size: 1.5em;
  }

  .loginleft p {
    font-size: 0.9em;
  }

  .footer {
    padding: 15px 10px;
  }

  .container {
    flex-direction: column;
    margin-top: 20px;
  }

  .loginright {
    margin-right: 0;
  }
}

@media (max-width: 480px) {
  .header {
    font-size: 16px;
  }

  .logotext {
    font-size: 18px;
  }

  .footer {
    padding: 15px 10px;
  }

  .container {
    padding: 10px;
  }

  .buttonsa {
    padding: 10px;
  }
}
