/* Header */
.header {
  background-image: url("Pictures/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
}

.header {
  background-color: rgb(250, 246, 246);
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Trebuchet MS", sans-serif;
  background-color: yellow;
}

.header .logo {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  padding-left: 30px;
}

/* Navigation */
.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
}

/* Footer */
.footer {
  background-image: url(Pictures/footer.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 20px;
  margin-top: 126px;
}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

/* Table Arrangement */
.tablearrangement {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 100px 10%;
  margin-top: 60px;
}

/* Padding */
.pad {
  padding-right: 30px;
}

/* Button Styling */
.button {
  padding: 10px;
  background-color: #263146;
  color: white;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  width: 250px;
  align-self: center;
}

.button:hover {
  background-color: #1b2331;
}

.topbutton {
  margin-left: 88px;
  margin-right: 88px;
}

/* Image */
.imagefamily {
  width: 400px;
  height: 300px;
  margin: 50px auto 0; /* Adds space at the top and centers the image */
  display: block;
}

/* Title */
.title {
  text-align: center;
}

/* Logo Container */
.logocontainer {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  margin-left: 10px; /* Adjust spacing between logo and text */
}

/* Links */
.Link {
  text-decoration: none;
  color: black;
}

/* Buttons */
.buttons {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttons:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttons:active {
  background-color: #222;
  transform: scale(1);
}

/* Media Queries for Responsive Design */
@media (max-width: 1024px) {
  .header {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .logo-container {
    justify-content: center;
  }

  .nav ul {
    flex-direction: column;
    gap: 10px;
  }

  .tablearrangement {
    padding: 60px 5%;
    margin-left: 0;
    margin-right: 0;
  }

  .imagefamily {
    width: 80%;
    height: auto;
  }
}

@media (max-width: 768px) {
  .header {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .nav ul {
    flex-direction: column;
    gap: 10px;
  }

  .button {
    width: 100%;
    padding: 15px;
  }

  .imagefamily {
    width: 100%;
    height: auto;
  }

  .tablearrangement {
    flex-direction: column;
    padding: 50px 10%;
    margin-top: 30px;
  }
}

@media (max-width: 480px) {
  .header {
    font-size: 18px;
  }

  .tablearrangement {
    padding: 20px 5%;
  }

  .imagefamily {
    width: 100%;
    height: auto;
  }

  .button {
    width: 100%;
    padding: 20px;
  }

  .footer {
    padding: 15px 10px;
  }

  .logotext {
    font-size: 20px;
  }
}
