/* Header Styling */
.header {
  background-image: url("Pictures/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: yellow;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Trebuchet MS", sans-serif;
}

/* Logo Styling */
.header .logo {
  font-size: 24px;
  font-weight: bold;
}

/* Navigation Styling */
.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
}

/* Table Styling */
.table {
  width: 65%;
  margin: 0 auto;
  border-collapse: collapse;
}

.td {
  padding: 20px;
  vertical-align: middle;
  text-align: center;
}

.tableText {
  width: 50%;
}

.tableImage {
  width: 100%;
  max-width: 400px;
  height: auto;
}

/* Form Styling */
.form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: auto;
}

.label {
  margin-top: 10px;
  font-weight: bold;
  color: black;
  font-size: 12px;
}

.textInput, .fileInput, .textareas {
  margin-top: 5px;
  padding: 8px;
  font-size: 16px;
}

.button {
  margin-top: 25px;
  padding: 10px;
  background-color: #263146;
  color: white;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  width: 250px;
  align-self: center;
  transition: background-color 0.3s, transform 0.2s;
}

.button:hover {
  background-color: #1b2331;
}

.button:active {
  background-color: #222;
  transform: scale(1);
}

/* Footer Styling */
.footer {
  background-image: url("Pictures/footer.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 20px;
  margin-top: 20px;
}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

/* Layout and Spacing */
.spacetop {
  margin-top: 130px;
}

.normaltext {
  color: black;
  text-align: justify;
}

.logocontainer {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  margin-left: 10px;
}

/* Padding helper */
.pad {
  padding-right: 30px;
}

/* Link Styling */
.Link {
  text-decoration: none;
  color: black;
}

/* Button Styling */
.buttons {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttons:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttons:active {
  background-color: #222;
  transform: scale(1);
}
