.header {
  background-image: url("Pictures/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
}

.header {
  background-color: rgb(250, 246, 246);
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Trebuchet MS", sans-serif;
  background-color: yellow;
}

.header .logo {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  padding-left: 30px;
}

.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
}

.footer {
  background-image: url(Pictures/footer.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 20px;
  margin-top: 20px;
}

.pad {
  padding-right: 30px;
}

.hero {
  margin-top: 61px;
  background-image: url("Pictures/kid1.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  padding: 30px 20px;
  text-align: center;
}

.titleboldtext {
  color: black;
  font-size: 18px;
  font-weight: bold;
}

.boldtext {
  color: black;
  font-size: 16px;
  font-weight: bold;
}

.normaltext {
  color: black;
  text-align: justify;
}

.imageresize {
  width: 400px;
  display: block;
  margin: 0 auto;
}

.faqcontainer {
  width: 80%;
  border-radius: 8px;
  padding: 16px;
  margin: 0 auto;
}

.expandbutton {
  background: none;
  border: none;
  color: #777;
  font-size: 14px;
  cursor: pointer;
  transform: rotate(0);
  transition: transform 0.3s;
}
.answer {
  display: none;
  padding: 8px 0 0;
  font-size: 14px;
}

.expandbutton.open {
  transform: rotate(180deg);
}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}


.logocontainer {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  margin-left: 10px; /* Adjust spacing between logo and text */
}

.Link{
  text-decoration:none;
  color:black;
}

.buttons {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttons:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttons:active {
  background-color: #222;
  transform: scale(1);
}