.bodyhome {
  font-family: "Trebuchet MS", sans-serif;
  margin: 0;
  padding: 0;
  line-height: 1.6;
  overflow-x: hidden;
}

.headerhome {
  background-image: url("Pictures/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: black;
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: yellow;
  padding-bottom:-20px;
}

.header .logo {
  font-size: 24px;
  font-weight: bold;
  padding-left: 30px;
}

.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
}

.texttitle {
  font-size: 2.5rem;
  margin-top: 150px;
}

.text {
  margin-top: 10px;
}

.buttonlearnmore {
  display: block;
  margin: 30px auto;
  padding: 10px;
  background-color: #263146;
  color: white;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  width: 250px;
}

.aboutustitle {
  font-size: 50px;
  text-align: center;
  margin-top: 140px; 
}

.aboutustext {
  font-size: 20px;
  text-align: justify;
  margin-top: 10px;
  margin-left: 15%;
  margin-right: 15%;
  margin-bottom: 170px;
}

.footerhome {
  background-image: url(Pictures/footer.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 20px;
  margin-top: 20px;
}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

.pad {
  padding-right: 30px;
}

.introsection {
  width: 60%; 
  margin: 0 auto;
  text-align: center;
}
.centertable {
  width: 60%;
  margin: 50px auto;
  text-align: center;
  border-collapse: collapse;
}

.centertable td {
  text-align: center; 
  vertical-align: middle; 
  padding: 20px;
}

.logocontainer {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  font-family: "Trebuchet MS", sans-serif;
  margin-left: 10px;
}

.Link{
  text-decoration:none;
  color:black;
}

.buttons {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttons:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttons:active {
  background-color: #222;
  transform: scale(1);
}