/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Trebuchet MS", sans-serif;
  background-color: #f9f9f9;
}

/* Header */
.header {
  background-image: url("Pictures/header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 80px;
  color: black;
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: yellow;
  z-index: 1000;
}

.header .logo {
  font-size: 24px;
  font-weight: bold;
  padding-left: 20px;
}

.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
  padding-right: 30px;
}

.nav ul li {
  display: inline;
}

.nav ul li a {
  color: black;
  text-decoration: none;
}

.nav ul li a:hover,
.nav ul li a:focus {
  text-decoration: underline;
}

/* Logo Section */
.logocontainer {
  display: flex;
  align-items: center;
}

.logotext {
  font-size: 24px;
  font-weight: bold;
  margin-left: 10px;
}

/* Unban Section */
.unban {
  max-height: 700px;
  width: 80%;
  margin: 5% auto;
  padding: 20px;
  background-color: #ffffff;
  border: 1px solid #ccc;
  border-radius: 8px;
}

.unban h1 {
  text-align: center;
  margin-bottom: 20px;
}

.unban form {
  border: 1px solid black;
  padding: 20px;
  border-radius: 8px;
}

#web {
  width: 90%;
  height: 50px;
  margin: 10px auto;
  display: block;
  text-align: center;
}

.button {
  margin: 10px auto;
  padding: 10px;
  background-color: #263146;
  color: white;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  width: 250px;
  display: block;
  transition: background-color 0.3s;
}

.button:hover {
  background-color: #1b2331;
}

.label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}

.unbanlist {
  max-height: 450px;
  overflow-y: auto;
  font-size: 20px;
  border: 1px solid black;
  padding: 10px;
  margin-top: 20px;
  border-radius: 8px;
}

.unban table {
  width: 100%;
  border-collapse: collapse;
}

.unban td {
  text-align: left;
  font-size: 15px;
  font-weight: bold;
}

.unbanline button {
  float: right;
}

.lock {
  height: 50px;
  width: 50px;
}

/* Footer */
.footer {
  background-image: url("Pictures/footer.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  text-align: center;
  padding: 10px 0;
  position: absolute;
  bottom: 0;
  width: 100%;
}

.footerlinks a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

.footerlinks a:hover {
  text-decoration: underline;
}

/* Select Inputs */
.selects {
  margin: 10px 0;
  padding: 10px;
  color: black;
  border: 1px solid black;
  width: 250px;
  cursor: pointer;
}

.attribute {
  width: 30%;
}

.selects {
  width: 100%;
}

/* Buttons */
.buttons {
  background-color: black;
  color: white;
  border: none;
  padding: 5px 20px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.buttons:hover {
  background-color: #444;
  transform: scale(1.05);
}

.buttons:active {
  background-color: #222;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .header {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .nav ul {
    flex-direction: column;
    gap: 10px;
  }

  .unban {
    width: 90%;
  }

  .button {
    width: 100%;
  }

  .unbanlist {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .header {
    font-size: 18px;
  }

  .unban {
    padding: 15px;
  }

  .button {
    padding: 15px;
  }

  .footer {
    padding: 15px 10px;
  }
}

@media (max-width: 480px) {
  .header {
    font-size: 16px;
  }

  .logotext {
    font-size: 18px;
  }

  .unban {
    padding: 10px;
  }

  .button {
    padding: 20px;
  }
}
.Link {
  text-decoration: none;
  color: black;
}